














































































import Vue from 'vue';
import {mapGetters} from 'vuex';
import Navigation from '@/components/Navigation.vue';

export default Vue.extend({
  name: 'Workout',
  computed: {
    ...mapGetters({
      workout: 'getActiveWorkout'
    })
  },
  methods: {
    finishWorkout() {
      this.$store.commit('finishActiveWorkout');
      this.goBack();
    },
    cancelWorkout() {
      this.$store.commit('cancelActiveWorkout');
      this.goBack();
    },
    goBack() {
      this.$router.replace('/');
    }
  },
  components: {
    Navigation
  }
});
